import axios from 'axios'
import { Message, MessageBox } from 'element-ui'
import Cookies from 'js-cookie'
import store from '../store'

const fetchInstance = axios.create({
  baseURL: process.env.VUE_APP_BASE_API
  // timeout: 2000,
})

// request
fetchInstance.interceptors.request.use(config => {
  if (store.getters.token) {
    config.headers['DS-Token'] = Cookies.get('DS-Token')
  }
  return config
}, error => {
  console.log(error) // for debug
  Promise.reject(error)
})

// response
fetchInstance.interceptors.response.use(
  response => {
    const res = response.data
    if (res.code !== 20000) {
      Message({
        message: res.message,
        type: 'error',
        duration: 5 * 1000
      })

      // 40003:
      if (res.code === 40003) {
        MessageBox.confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          store.dispatch('FedLogOut').then(() => {
            location.reload()// vue-router bug
          })
        })
      } else {
        // 
        // Raven.captureException(requestErrorFormat(response))
        console.log('err:' + requestErrorFormat(response))// for debug
      }
      return Promise.reject('request error')
    } else {
      return response.data
    }
  },
  error => {
    if (error.code === 429) {
      Message({
        message: '',
        type: 'error',
        duration: 5 * 1000
      })
    } else {
      console.log('err:' + error)// for debug
      Message({
        message: error.message,
        type: 'error',
        duration: 5 * 1000
      })
    }

    // Raven.captureException(error)
    return Promise.reject(error)
  }
)

export default fetchInstance

function requestErrorFormat(res) {
  const o = {
    url: res.request && res.request.responseURL,
    res: res.request && res.request.response
  }
  return JSON.stringify(o)
}
